import { Link } from 'gatsby'
import React from 'react'
import * as styles from './footer.module.css'
import Svg1 from '../../images/svg/phone.svg'
import Svg2 from '../../images/svg/email.svg'

const Footer = () => {
  return (
    <footer>
      <div className={styles.footer}>
        <div className={styles.footerContainer}>
          <div className={styles.footerBox}>
            <div className={styles.footerContacts}>
              <span className={styles.footerText}>
                Frudee Web Studio develops sites of any complexity on a turnkey
                basis on our own platform.
              </span>
              <div className={styles.footerContact}>
                <Svg1 />
                <div>
                  <span>Phone</span>
                  <span>+7 982 324 16 84</span>
                </div>
              </div>
              <div className={styles.footerContact}>
                <Svg2 />
                <div>
                  <span>Email</span>
                  <span>support@frudee.ru</span>
                </div>
              </div>
            </div>
            <div className={styles.footerFlex}>
              <div className={styles.footerLogo}>
                <span>Frudee Web Studio</span>
              </div>
              <div className={styles.footerLinks}>
                <ul>
                  <li>
                    <Link to="#">About</Link>
                  </li>
                  <li>
                    <Link to="#">Prices</Link>
                  </li>
                  <li>
                    <Link to="#">Home</Link>
                  </li>
                  <li>
                    <Link to="#">Contacts</Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className={styles.footerBottom}>
            <span>Website created by Frudee Web Studio</span>
            <span>Copyright C 2022 All rights reserved</span>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default Footer
