import React from 'react'
import AdvantCard from '../UI/advantagesCard'
import * as styles from './advantages.module.css'

const Advantages = () => {
  return (
    <section>
      <div className={styles.advantages}>
        <span>Advantages</span>

        <h2> Solutions That Work for Your Success </h2>
        <span className={styles.advantagesDesc}>
          When working with Weblium Studio, you get more than just a beautiful
          site. We find exclusive solutions to your problems and offer a wide
          range of services.
        </span>
        <div className={styles.cardContainer}>
          <AdvantCard className={styles.item} svg={1} />
          <AdvantCard className={styles.item} svg={2} />
          <AdvantCard className={styles.item} svg={3} />
        </div>
      </div>
    </section>
  )
}

export default Advantages
