import { Link } from 'gatsby'
import React from 'react'
import * as styles from './order.module.css'

const Order = () => {
  return (
    <section>
      <div className={styles.order}>
        <span>Send us a request</span>
        <h2>Need a new website?</h2>
        <span>
          Leave your contacts, and we will contact you to clarify the details
          and start the site development!
        </span>
        <Link to="#">Order website</Link>
      </div>
    </section>
  )
}

export default Order
