import { Link } from 'gatsby'
import React from 'react'
import PriceCard from '../UI/priceCard'
import * as styles from './prices.module.css'
import Svg1 from '../../images/svg/dollar.svg'

const Prices = () => {
  const pricePlans = [
    {
      name: 'Start',
      price: '250$',
      desc: 'This option is for people who do not have time for website development.',
      features: ['Cool', 'Super', 'Nice'],
    },
    {
      name: 'Base',
      price: '450$',
      desc: 'This option is for people who do not have time for website development.',
      features: ['Cool', 'Super', 'Nice'],
    },
    {
      name: 'Ultimate',
      price: '850$',
      desc: 'This option is for people who do not have time for website development.',
      features: ['Cool', 'Super', 'Nice'],
    },
  ]

  return (
    <section>
      <div className={styles.prices}>
        <span className={styles.uiSmallText}>Our prices</span>
        <h2>How much does it cost</h2>
        <span className={styles.uiSmallText}>
          We offer five pricing plans, depending on your needs.
        </span>
        <div className={styles.cardContainer}>
          {pricePlans.map((el) => {
            return <PriceCard price={el} key={el.name} />
          })}
        </div>
        <div className={styles.compareRates}>
          <Svg1 />
          <h3>Compare Rates</h3>
          <span>
            Find out in more detail how the tariffs differ from each other and
            select the best option
          </span>
          <Link to="#">LEARN MORE</Link>
        </div>
      </div>
    </section>
  )
}

export default Prices
