import React from 'react'
import { graphql, Link, useStaticQuery } from 'gatsby'
import * as styles from './welcome.module.css'

const Welcome = () => {
  return (
    <section>
      <div className={styles.promo}>
        <div className={styles.promoInner}>
          <span>
            Effective websites and the best solutions for your business offered
            by experts
          </span>
          <h1>Frontend Web Studio</h1>
          <span>
            Our team of professionals develop stylish websites with exclusive
            designs that really boost your business and generate profit. Contact
            us now for more details.
          </span>
          <div className={styles.buttonGroup}>
            <Link to="#">Order</Link>
            <Link to="#">About</Link>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Welcome
