import React from 'react'
import * as styles from './advantagesCard.module.css'
import Svg1 from '../../images/svg/interactivity.svg'
import Svg2 from '../../images/svg/glow.svg'
import Svg3 from '../../images/svg/startup.svg'

const AdvantCard = (props) => {
  return (
    <div className={`${styles.card} ${props.className}`}>
      {props.svg === 1 && <Svg1 />}
      {props.svg === 2 && <Svg2 />}
      {props.svg === 3 && <Svg3 />}
      <span>Card heading</span>
      <span>
        The goal of any business is to get more sales. Our task is to help you
        with that. We carry out an in-depth analysis of your niche and create
        sites that generate profit.
      </span>
    </div>
  )
}

export default AdvantCard
